<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered" style="max-width:1200px !important">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('Attachments') }}
                        </h5>
                        <button v-on:click="ClosePopup()" type="button" class="close" data-original-title="Close"><em
                                class="fa fa-times"></em></button>

                    </div>
                    <div class="modal-body">
                        <div class="listing tblassestgroupinfodetails px-1 mt-3">
                            <div class="table-responsive ">
                                <table id="tblassestgroupinfodetails" class="table table-bordered dt-responsive nowrap mt-0"
                                    style="width:100%">
                                    <thead class="thead-bg">
                                        <tr>
                                            <th>{{ $t('SerialNumber') }}</th>
                                            <th>{{ $t('AttachmentName') }}</th>
                                            <th>{{ $t('FileName') }}</th>
                                            <th>{{ $t('ForTermCondition') }}</th>
                                            <th>{{ $t('TermConditionStartDate') }}</th>
                                            <th>{{ $t('Action') }}</th>
                                        </tr>
                                    </thead>
                                    <tbody>
                                        <tr v-for="(item, index) in arryaofData" :key="index">
                                            <td class="text-overflow-dynamic-container w-auto">
                                                {{ item.SERIAL_NUMBER }}
                                            </td>
                                            <td>
                                                <span class="text-overflow-dynamic-container w-auto">{{
                                                    item.ATTACHMENT_TITLE }}</span>
                                            </td>
                                            <td>
                                                <span class="text-overflow-dynamic-container w-auto">{{ item.ATTACHMENT_NAME
                                                }}</span>
                                            </td>
                                            <td>
                                                <span class="text-overflow-dynamic-container w-auto">{{
                                                    item.IS_FOR_TERMS_CONDITION }}</span>
                                            </td>
                                            <td>
                                                <span class="text-overflow-dynamic-container w-auto">{{
                                                    item.TERMS_CONDITION_STARTS_AT }}</span>
                                            </td>
                                            <td class="text-center single-action">
                                                <a href="javascript:;" @click="Downloadfiles(item)" class="downloadfile"
                                                    title="Download Attachment">
                                                    <em class="fa fa-download text-primary action-icon"
                                                        title="Download Attachment"></em>
                                                </a>
                                                <a href="javascript:;"
                                                    @click="DeleteAtachmentFile(item.ASSET_ATTACHMENT_ID, item.ASSET_CATALOGUE_ID)"
                                                    class="deletefile mr-2" title="Remove Attachment">
                                                    <em class="fa fa-trash text-danger action-icon"
                                                        title="Remove Attachment"></em>
                                                </a>
                                            </td>
                                        </tr>
                                    </tbody>
                                </table>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>
<script>
import DataService from '../../service/DataService';

export default {
    props: {
        AssetDetails: {
            type: String
        }
    },
    data() {
        return {
            arryaofData: []
        }
    },
    created: function () {
        var vm = this;
        vm.GetViewFiles();
    },
    methods: {
        ClosePopup() {
            var vm = this;
            vm.$parent.CloseViewFilePopup();
        },
        GetViewFiles() {
            debugger;
            var vm = this;
            var obj = `catalogueId=${vm.AssetDetails.ASSET_CATALOGUE_ID}&isDelete=1&flag=`
            DataService.AttachViewFiles(obj).then(response => {
                vm.arryaofData = JSON.parse(response.data).data;
            })
        },
        Downloadfiles(item) {
            debugger;
            var param = `filePath=${item.ATTACHMENT_PATH}`;
            DataService.DownloadAttachment(param).then(function (response) {
                const byteData = response.data;
                const fileURL = `data:${item.ATTACHMENT_TYPE};base64,${byteData}`;
                var fileLink = document.createElement('a');
                fileLink.href = fileURL;
                fileLink.setAttribute('download', item.ATTACHMENT_NAME);
                document.body.appendChild(fileLink);
                fileLink.click();
            });
        },
        DeleteAtachmentFile(fileId, catId) {
            debugger

            var vm = this;
            var url = `fileId=${fileId}&catId=${catId}`;
            DataService.DeleteViewAttachmentFile(url).then(response => {
                vm.confirmR(vm.$t("ConfirmDelete"), true, (vm.$t("Delete") + "  " + vm.$t("REPORT_ID")), function () {

                })
                vm.$parent.CloseViewFilePopup();
                   

            })
        }
    }

}
</script>
