
<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered" style="max-width:900px !important">
                <div class="modal-content">
                    <div class="modal-header theme-primary partition-full">
                        <h4 class="modal-title">{{ $t('HAS_RELATED_PRODUCTS') }}</h4>

                        <button type="button" class="close" v-on:click="ClosePopup">
                            <em class="fa fa-times" aria-hidden="true"></em>
                        </button>
                    </div>
                    <div class="listing py-3 px-3">
                        <div class="table-responsive table-fix-header" id="div_forListView">
                            <table class="table table-bordered mt-0" style="width: 100%;" id="tblRElatedProductsList">
                                <thead class="thead-bg">
                                    <tr>

                                        <th>
                                            <span>{{ $t('PRODUCT_NAME') }}</span>
                                        </th>
                                        <th>
                                            <span>{{ $t('Mandatory_Non_Mandatory') }}</span>
                                        </th>

                                    </tr>
                                </thead>
                                <tbody>
                                    <tr v-for="item in RelatedProductsData">

                                        <td>
                                            <span class="text-overflow-dynamic-container">
                                                {{ item.NAME }}
                                            </span>
                                        </td>
                                        <td>
                                            <span class="text-overflow-dynamic-container">
                                                {{ item.Mandatory == true ? 'Mandatory' : 'NonMandatory'}}
                                            </span>
                                        </td>

                                    </tr>
                                    <tr>
                                        <td  v-if="RelatedProductsData.length==0" colspan="7" class="text-danger text-center no-record">No Record Found</td>
                                    </tr>
                                </tbody>
                            </table>
                        </div>
                        <div class="modal-footer">
                                <button type="button" class="btn btn-danger" v-on:click="ClosePopup">Close</button>
                            </div>
                    </div>

                </div>
            </div>
        </div>
    </div>
</template>

<script>
import DataService from '../../service/DataService'
export default{
    props:{
        AssetCatId:{
            type:String
        }
    },
    data(){
        return{
            RelatedProductsData:[]
            
        }
    },
    created: function()
    {
        var vm=this;
        vm.FetchData()
    },
    methods:{
        FetchData()
        {
            debugger
            var vm=this;
            vm.isLoading = true;
            var prams = `catId=${vm.AssetCatId}&type=ProductName`;
            DataService.GetRelatedProduct(prams).then(response => {
                debugger
                vm.RelatedProductsData=JSON.parse(JSON.parse(response.data.result).RESULT);
            })
        },
        ClosePopup()
        {
            var vm=this;
            vm.$parent.CloseRelatedProductsPopup()
        }
    }
}
</script>