<template>
    <div class="my-popups">
        <div class="modal d-block">
            <div class="modal-dialog modal-dialog-centered">
                <div class="modal-content">
                    <div class="modal-header">
                        <h5 class="modal-title">
                            {{ $t('CloneProduct') }}
                        </h5>
                        <button v-on:click="ClosePopup()"  type="button" class="close" data-original-title="Close"><em
                                class="fa fa-times"></em></button>

                    </div>
                    <div class="border p-3">
                        <dynamic-form lang="en" v-if="DataLoaded" :buttons="buttons" :schema="FormSchema" v-on:OnSubmit="onSubmit"></dynamic-form>                       
                        <div class="col-lg-6">
                            <div class="py-2">
                                <span class="mandatory"> {{ $t('MandatoryString') }} </span>
                            </div>
                        </div>                       
                    </div> 
            </div>
        </div>
    </div>
</div>
</template>

<script>
import DataService from '../../service/DataService'
export default {
    props:{
        AssetCatCloneId:{
            type:String
        }
    },    
    data() {
        return { 
            DataLoaded:0,           
            FormSchema: [
                {
                    layoutType: "single",
                    Data: [                                
                        {
                            astype: "TextField",
                            label: this.$t('Product'),
                            name: "Title",
                            value: "",
                            placeholder: "",
                            validationRules: { "required": true, max: 250 },                            
                        },
                    ]
                }
            ],
            buttons: [
                {
                    type: "submit",
                    class: "btn btn-success",
                    text: "<i class='fa fa-save pr-2'></i>Save",
                },

                {
                    type: "button",
                    class: "btn btn-danger",
                    text: "<i class='fa fa-close pr-2'></i>Cancel",
                    onClick: this.ClosePopup
                }
            ]
        }
    }, 
    created:function(){
        this.DataLoaded=1;
    },
    methods: {
        ClosePopup()
        {
            debugger
            var vm=this;
            vm.$parent.ShowClonePopup=false;
        },
        onSubmit(formfields, val)
        {            
            var  vm=this;
            let myObj = {};
            myObj.AssetcatalogId = vm.AssetCatCloneId;
            myObj.ProductName=formfields.Title;
            var dataval= JSON.stringify(myObj)         
            DataService.CloneProductSave(dataval).then(response => {                
                if(response != ""){
                    if (response.data =='ALREADYEXIST') {                      
                        vm.ShowAlert(vm.$t("Product name already exist"), "warning", vm.$t('Alert'));
                    }
                    else if (response.data =='CLONE') {                       
                        vm.ShowAlert(vm.$t("Product Clone successfully"), "success", vm.$t('Alert'));
                        vm.ClosePopup();  
                        vm.$parent.FetchData();                     
                    }
                    else {                       
                        vm.ShowAlert(vm.$t("Product Clone failed"), "failure", vm.$t('Alert')); 
                    }
                }else{
                    vm.ShowAlert(vm.$t("Product Clone failed"), "failure", vm.$t('Alert'));
                }
            })

        }
      
    }
}
</script>